___CSS_LOADER_EXPORT___.push([module.id, `.cookies-container {
  width: 40%;
  max-width: 1000px;
  position: fixed;
  bottom: 60px;
  background-color: var(--background-color-primary);
  border-radius: 5px;
  box-shadow: 2px 2px 4px gray;
  padding: 10px 10px;
}
.cookies-container .titre {
  display: flex;
  justify-content: center;
}
.cookies-container .titre .span-image {
  flex: 1;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center/contain no-repeat;
}
.cookies-container .titre p {
  flex: 3;
  margin: 25px 0;
  font-size: 12px;
}
.cookies-container p {
  font-size: 7px;
  margin: 20px 0 20px 0;
}
.cookies-container .button-container {
  display: flex;
  justify-content: right;
  flex-wrap: wrap;
  margin-bottom: 10px;
}
.cookies-container .button-container p {
  cursor: pointer;
  padding: 15px 35px;
  border-radius: 5px;
  left: 50px;
  font-size: 10px;
  font-weight: bold;
  margin: 0 0 0 0;
  transition: all 0.25s ease-in;
}
.cookies-container .button-container p.yes-button {
  background-color: var(--blue-primary);
  color: white;
  margin-left: 35px;
}
.cookies-container .button-container p.yes-button:hover {
  background-color: white;
  color: var(--blue-primary);
}
.cookies-container .button-container p.no-button {
  background-color: white;
  color: var(--background-color-primary);
}
.cookies-container .button-container p.no-button:hover {
  color: var(--blue-primary);
}

@media (max-width: 600px) {
  .cookies-container .button-container {
    display: block;
  }
  .cookies-container .button-container p.yes-button {
    margin-top: 25px;
  }
}
@media (max-width: 700px) {
  .cookies-container .button-container {
    justify-content: space-evenly;
  }
  .cookies-container .button-container p.yes-button {
    margin-left: 0;
  }
}
@media (max-width: 979.9px) {
  .cookies-container {
    width: 50%;
  }
